/* footer */
.footer{
    padding:40px;
    a{
        font-size:$font-size-s;
        text-decoration: none;
        color:#fff;
    }
    .footer-navber-wrap{
        border-bottom: 1px solid #fff;
        margin-bottom: 24px;
        @include pad{
            margin-bottom: 14px;
        }
    }
    .footer-navber-list{
        margin-bottom: 24px;
        @include pad{
            display: none;
        }
        li{
            font-weight: bold;
            margin-right:48px;
        }
    }   
    p{
        margin-bottom: 17px;
        @media (max-width:992px){
            margin-bottom: 11px;
        }
        a{
            font-size: $font-size-l+4;
            @media (max-width:992px){
                font-size: $font-size-m;
            }
            @include pad{
                font-size: $font-size-s;
            }
        }
        img{
            width: 48px;
            margin-right:17px;
            @media (max-width:992px){
                width: 24px;
            }
        }
    }
    .footer-navber-logo{
        margin-bottom: 36px;
        margin-left: auto;
        li{
            margin-left: 32px;
            @include pad{
                margin-left:20px;
            }
        }
        img{
            width: 48px;
            @media (max-width:992px){
                width:24px;
            }
        }
    }
    .footer-brand{
        @include pad{
            flex-direction: column;
            align-items: start;
        }
        li{
            margin-bottom: 7px;
        }
        li:nth-of-type(1){
            margin-right: 38px;
            @media (max-width:992px){
                display: none;
            }
        }
        li:nth-of-type(3),li:nth-of-type(4){
            display: none;
            @include pad{
                display: block;
            }
        }
        a{
            font-size: $font-size-s;
            @include pad{
                font-size: 14px;
            }
        }
        span{
            @media (max-width:992px){
                font-size:$font-size-s;
            }
            @include pad{
                font-size: 14px;
            }
        }
    }
    .policy{
        margin-left: auto;
        @include pad{
            display: none;
        }
        li:nth-of-type(1) {
            margin-right: 62px;
        }
        a{
            font-size: 14px;
        }
    }
    .footer-logo{
        display: none;
        @include pad{
            display: block;
            margin-top: 50px;
        }
        img{
            width: 46px;
        }
       
    }
}