// 容器
.container{
    max-width: 1110px;
    margin: 0 auto;
    @media(max-width:992px){
        max-width:690px;
    }
    @include pad{
        max-width:345px;
    }
}
//logo專用
@mixin text-hide{
    text-indent: 101%;
    white-space: nowrap;
    overflow: hidden;
}
//hegiht
.h-100{
    height: 100%;
}
// flex系列
.d-flex{
    display: flex;
}
.flex-row-reverse{
    flex-direction: row-reverse;
}
.flex-column{
    flex-direction: column;
}
.flex-column-reverse{
    flex-direction: column-reverse;
}
.justify-content-center{
    justify-content: center;
}
.justify-content-between{
    justify-content: space-between;
}
.justify-content-around{
   justify-content: space-around;
}
.justify-content-end{
   justify-content: flex-end;
}
.align-items-center{
    align-items: center;
}
.align-items-end{
    align-items:flex-end;
}

// font-size
.text-xl{
    font-size: $font-size-xl;
}
.text-l{
    font-size: $font-size-l;
}
.text-m{
    font-size: $font-size-m;
}
.text-s{
    font-size: $font-size-s;
}
.text-right{
    text-align: right;
}
.text-center{
    text-align: center;
}
.text-left{
    text-align: left;
}
.text-bold{
    font-weight: bold;
}
// font-color
.text-primary{
    color:#AA0601;
}
.text-light{
    color:#fff;
}
// bg
.bg-primary{
    background:#AA0601;
}
.bg-secondary{
    background:#AA06010D;
}

