/* banner */
.banner{
    height: 840px;
    background-image: url("https://upload.cc/i1/2020/11/03/BDbS45.png ");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    font-weight: bold;
    @include pad{
        background-image: url("https://upload.cc/i1/2020/11/03/8ukMzQ.png");
        height: 394px;
    }
    p{
        font-size: $font-size-l+4;
        color:#650300;
        margin-bottom: 10px;
        line-height: 1.5;
        @include pad{
            font-size:$font-size-m;
        }
        @include mobile{
            font-size: $font-size-s;
        }
    }
    h2{
        color:#650300;
        @include pad{
            font-size:$font-size-m*1.9;
        }
        @include mobile{
            font-size:30px;
        }

    }
}
/* service */
.service{
    background: #AA06010D;
    padding: 64px  0px;
    @media (max-width:992px) {
        padding: 40px 0px;  
    }
    @include  pad {
        padding: 32px  0px;
    }
    .container{
        @include pad{
            max-width:768px
        }
    }
    h3{
        font-size: $font-size-m*2.4;
        font-weight: bold;
        margin-bottom: 60px;
        @media (max-width:992px) {
           font-size: $font-size-l;
           margin-bottom: 32px;
        }
        @include pad{
            font-size:$font-size-m+4;
            margin-bottom: 20px;
        }
    }
    .service-list{
        flex-wrap: wrap;
        align-items: baseline;
    }
    li{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 18%;
        margin-bottom: 40px;
        @media (max-width:992px){
            width: 36%;
        }
        @include pad{
            width: 60%;
        }
        img{
            margin-bottom: 40px;
            transition:0.5s;
            @media (max-width:992px){
                width: 80px;
                margin-bottom: 16px;
            }
        }
        img:hover{
            transform: rotateY(180deg)translateY(-10px);
        }
        h4{
            font-size: 24px;
            font-weight: bold;
            margin-bottom: 4px;
            @media (max-width:992px){
                font-size: $font-size-m;
                margin-bottom: 8px;
            }
            @include pad{
               font-size: $font-size-m;
               
            }
        }
        p{
            font-size: 16px;
            color:black;
            text-align:justify;
            line-height: 1.5;
            font-weight: bold;
        }
    }
    
}
/* series-products */
.series-products{
    padding: 64px 0px 80px 0px;
    font-weight: bold;
    @media (max-width:992px){
        padding: 40px 0px;
    }
    h3{
        font-size:$font-size-m*2.4;
        margin-bottom: 60px;
        @media (max-width:992px){
            font-size:$font-size-m*2;
            margin-bottom: 32px;
        }
        @include pad{
            font-size:$font-size-l;
        }
    }
    li{
        margin-bottom: 32px; 
    }
    .series-products-hide-text{
        position: relative;
        width: 55%;
        @include pad{
            width: 100%;
        }
        .change-bg{
            filter: grayscale(100%);
            transition: 1s;
        }
        .change-bg:hover{
            filter: grayscale(0%);
        }
        .hide-icon{
            display: none;
            @include pad{
                display: block;
                position: absolute;
                top:35%;
                left:22%;
            }
        }
        h4{
            display: none;
            @include pad{
                display: block;
                position: absolute;
                top:35%;
                left:35%;
                color:#fff;
                font-size: $font-size-m*1.4;
            }
        }
    }
    .series-products-icon{
        margin-right: 8px;
        @media (max-width:992px){
            width: 32px;
            height: 32px;
        }
    }
    .series-products-hide-wrap{
        @media (max-width:992px){
            flex-direction: row;
            justify-content: flex-start;
        }
    }
    .series-products-text{
        margin: 0px 30px;
        @media (max-width:992px){
            margin:0px;
            margin-left: 30px;
        }
        @include pad{
            display: none;
        }
    }
    h4{
        font-size:$font-size-m*2.4;
        @media (max-width:992px){
            font-size:$font-size-m+8;
        }
      
    }
}
/* 聯名co-branding */
.co-branding{
    background-image: url("https://upload.cc/i1/2020/11/03/oBzGXf.png");
    background-size: cover;
    background-position: center center;
    padding: 64px 0px 40px 0px;
    font-weight: bold;
    h3{
        font-size: $font-size-m*2.4;
        margin-bottom: 60px;
        @include pad{
            font-size:$font-size-l;
        }
        @include mobile{
            font-size:$font-size-m;
            margin-bottom: 20px;
        }
    }
    .co-branding-img{
        @media (max-width:992px){
            flex-wrap: wrap;
        }
      
    }
    h4{
        font-size: 72px;
        position: absolute;
        white-space: nowrap;
        @media (max-width:992px){
            font-size: $font-size-l;
        }
        @include mobile{
            font-size: $font-size-m;
        }
    }
    li{
        width: 48%;
        position: relative;
        @media (max-width:992px){
            margin-bottom: 16px;
        }
        @include pad{
            width: 98%;
        }
    }
    li::before{
        content:"";
        position: absolute;
        top:0;
        bottom: 0;
        left: 0;
        right: 0;
        background:rgba(255, 255, 255, 0.5);
    }
    li:hover::before{
        background:rgba(255, 255, 255, 0);
        transition: 1s;
    }
}

 /* recommend */
.recommend{
    padding: 64px 0;
    h3{
        font-size: $font-size-m*2.4;
        font-weight: bold;
        margin-bottom: 60px;
        @media (max-width:992px){
            font-size: $font-size-l;
        }
        @include pad{
            font-size: $font-size-m;
        };
    }
    .recommend-list{
        @media (max-width:992px){
           flex-wrap: wrap;
        }
    }
    li{
        width: 23%;  
       
        @media (max-width:992px){
            width:48%;
            margin-bottom: 28px;
        }
        @include pad{
            width:98%;
        }
    }
    img{
        width: 100%;
        object-fit: cover;
    }
    img:hover{
        transform: rotateY(180deg);
        transition: 2s;
    }
    h4{
        margin-top: 14px;
        margin-bottom: 7px;
        font-size: 14px;
        font-weight: bold;
    }
    small{
        font-size: 14px;
        color:#707070;
    }
    P{
        font-size: 16px;
        line-height: 1.5;
        margin-bottom: 16px;
    }
    .card-body{
        width: 100%;
        padding: 10px;
        border: 1px solid #DEE2E6;
    }
}

/* tell-us */
.tell-us{
    background:#AA06010D;
    padding-top: 64px;
    padding-bottom: 80px;
    .wrap{
        max-width:730px;
        margin: 0 auto;
        @include pad{
            max-width:450px;
        }
        @include mobile{
            max-width: 310px;
        }
    }
    h3{
        font-size: 48px;
        margin-bottom: 24px;
        @media (max-width:992px){
            font-size: $font-size-s*2;
        }
        @include pad{
            font-size: $font-size-m;
        }
    }
    p{
        margin-bottom: 20px;
        line-height: 1.5;  
        @media (max-width:992px){
           font-size: $font-size-s;
        } 
    }
    .tell-us-container{
        max-width: 730px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
    }
    label,input,textarea{
        font-size: $font-size-m;
        margin-bottom: 16px; 
        @media (max-width:992px){
            font-size: 14px;
        }
    }
    .input-block{
        width: 100%;
        height: 36px;
    }
    .textarea-block{
        width: 100%;
        height: 100px;
    }
    .submit-wrap{
        margin-top: 20px;
        justify-content: center;
    }
    .submit-style {
        color:#fff;
        border: 0;
        padding: 8px 76px;
        cursor: pointer;
        @include pad{
            padding: 8px 54px;
        }
    }
    .submit-style:hover{
        background: orange;
    }

}
