/* navber */
.navber{
    padding: 30px 88px;
    @media (max-width:992px){
      flex-direction: column;
      padding: 0px;
      padding-top: 10px;
    }
    h1{
      @media (max-width: 992px){
          display: flex;
          justify-content: center;
          margin-bottom: 11px;
      }
      .navber-logo{
          text-decoration: none;
          display: block;
          background-image: url("https://upload.cc/i1/2020/11/03/6UCBJf.png");
          width: 80px;
          height: 48.57px;
          @include text-hide;        
        }
    }
    .navber-list{
      white-space:nowrap;
      @media (max-width:992px){
          flex-wrap: wrap;
          width: 100%;
      }
        li{
          margin-left: 64px;
          @media (max-width:992px){
              margin: 0px;
              text-align: center;
              border: 1px solid #fff;
              width: 50%;
              :hover{
                  background: orange;
              }
          }
          a{
              text-decoration: none; 
              display: block;
              color:white;
              font-size: 20px;
              font-weight: bold;
              padding: 10px 0px;
               //預防hover顯示跳動
              border-bottom: 2px solid transparent ;
              @media (max-width:992px){
                  padding:8px;
              }
              @include pad{
                  font-size: 14px;
                  padding: 10px 0px;
              }
          }
          a:hover{
              border-bottom: 2px solid #fff;
          }
        }  
    }
}